.modal {
  position: relative;
  width: 100%;
  padding: rem(29) rem(30) rem(46);
  background-color: $white;
  box-shadow: 0 5px 10px rgba($black, 0.25);

  @include tablet {
    padding: rem(38) rem(100) rem(45);
    width: rem(566);
  }

  &__close {
    display: none;
    position: absolute;
    right: rem(15);
    top: rem(15);
    width: rem(30);
    height: rem(30);
    padding: rem(20);
    margin: 0;
    background-color: transparent;
    border: none;
    transition: transform;
    transition-duration: 100ms;
  }

  &__close--show {
    display: initial;
  }

  &__close:hover &__close-line {
    background-color: #d16363;
    opacity: 0.6;
  }

  &__close:active &__close-line {
    background-color: #d16363;
    opacity: 0.4;
  }

  &__close-line {
    padding: 0;
    margin: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform-origin: center;
    transform: translateX(-50%) rotate(45deg);
    display: block;
    width: 70%;
    height: rem(2);
    background-color: $black;
    opacity: 0.6;
  }

  &__close-line:nth-of-type(2) {
    transform: translateX(-50%) rotate(-45deg);
  }

  &__title {
    text-align: center;
    font-size: rem(25);
    line-height: calc(30 / 25);
    font-weight: bold;
    text-transform: uppercase;
    color: $black;
    max-width: rem(160);
    margin: 0 auto;
    margin-block-end: rem(14);

    @include tablet {
      font-size: rem(30);
      line-height: calc(57 / 30);
      max-width: rem(380);
      margin-block-end: rem(11);
    }
  }


  &__desc {
    text-align: center;
    font-size: rem(15);
    line-height: calc(30 / 15);
    max-width: rem(160);
    margin: 0 auto;
    margin-block-end: rem(20);

    @include tablet {
      font-size: rem(17);
      line-height: calc(30 / 17);
      max-width: rem(380);
    }
  }

  &__choice-wrapper {
    overflow: hidden;
    display: flex;
    justify-content: center;
    gap: rem(7);
    margin-block-end: rem(30);
  }

  &__choice {
    appearance: none;
    width: 0;
    height: 0;
    position: absolute;
    left: -100vw;
    top: -100vh;
  }

  &__choice-name {

    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    font-size: rem(17);
    font-weight: bold;
    border: 2px solid rgba($off-black, 0.1);
    width: rem(82);
    height: rem(52);

    @include tablet {
      flex-grow: 1;
    }
  }

  &__choice-name:active {
    border-color: #62d1ba;
  }

  &__choice-name:hover {
    border-color: rgba($off-black, 0.3);
  }

  &__choice:checked+&__choice-name {
    border-color: #63d1bb;
  }


}
